.authorization {
    margin-top: 10%;
    background: #fff;
    padding-top: 20px;
    border-radius: 10px;
    width: 50%;
    box-shadow: inset 2px 1px 2px;
}

.auth__buttons {
    text-decoration: underline;
}

.loader {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
}

.container {
    margin-top: 3em;
}

.logo {
    padding-top: 4px;
}

.logo:hover {
    fill: #ede;
    filter: drop-shadow(1px 1px 6px rgba(255,255,255,0.7));
}

.product {
    background: #fff;
    box-shadow: 1px 3px 3px;
    padding: 0 !important;
}

.product__img {
    height: 150px !important;
    width: auto !important;
    margin: 0 auto;
}

.back_arrow {
    color: #000;
    font-size: 3em;
    cursor: pointer;
    position: fixed;
    left: 0;
}

.description__img {
    margin: auto;
    display: block;
    margin-bottom: 2em;
}

.description__divider {
    width: 100%;
    height: 2em;
    background: #def;
    box-shadow: inset 0px 2px 2px;
}

.description__divider div {
    width: auto;
    height: 50%;
    display: block;
    border-bottom: 3px dashed;
}

.description__text {
    font-size: 1.2em;
    text-align: justify;
    margin: 1em;
}

.review-info {
    display: grid;
    grid-template: auto auto / 1fr 1fr;
    box-shadow: 2px 2px 3px #000 !important;
    margin-bottom: 4px !important;
}

.review-info > p {
    grid-column: 1 / 3;
    border-top: 1px solid rgba(0,0,0,0.3);
    padding-top: 1em;
}

.vote {
    user-select: none;
    cursor: pointer;
}

.collection__stars {
    pointer-events: none;
}

.comment {
    z-index: 9;
}

.comment__text {
    padding: 10px !important;
    max-width: 100% !important;
    min-width: 100% !important;
    max-height: 6rem !important;
    min-height: 3rem !important;
}

.blackout {
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    width: 100vw;
    height: 100vh;
    background: rgba(0,0,0,0);
    animation: blackoutAnim 0.5s ease-in forwards; 
}
@keyframes blackoutAnim {
    to {
        background: rgba(0,0,0,0.3);
    }
}